.section2{
    background-color: #cccc33;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 90vh;
}
.myskills{
    display: flex;
    width: 75%;
    height: 73%;
    border: 1px solid;
    flex-direction: column;
    justify-content: space-between;
}
.myskills_header{
    width: 100%;
    height: 34%;
    border: 1px solid;
}
.myskills_header span{
    text-align: center;
    display: block;
}
.skill{
    display: flex;
    flex-direction:row;
    justify-content: space-between;
    height: 75%;
    width: 100%;
    background-color: #999;
}
.skill_item{
    height: 100%;
    width: 25%;
    background-color:white;
    display: flex;
    flex-direction: column;
}
.react_skills{
    width: 100%;
    height: 60%;
    border: 1px solid;
}
.skill_describe{
    width: 100%;
    height: 40%;
    border: 1px solid;
}